body {
	background: #2193b0 no-repeat fixed; /* fallback for old browsers */
	background: -webkit-linear-gradient(to bottom, #6dd5ed, #2193b0) no-repeat fixed;
	background: linear-gradient(to bottom, #6dd5ed, #2193b0) no-repeat fixed;

	font-family: 'Kumbh Sans', sans-serif;
}

header {
	padding-top: 20px;
	padding-bottom: 20px;
}


.error{
	background-color: $error;
	border-radius: 5px;
	width: 100%;
	height: 24px;
	display: grid;
	justify-content: center;
	align-content: center;
}